import { Box, Container, Typography, Button } from "@mui/material";
// import ActionFeed from "./components/ActionFeed";
import Banner from "./components/Banner";
import Jackpot from "./components/Jackpot";
import Role from "./components/Role";
import Winner from "./components/Winner";
import CurrentBattle from "./components/CurrentBattle";
import FinishedBattle from "./components/FinishedBattle";

const Home = () => {
  return (
    <Box sx={{ backgroundColor: "rgba(38,40,42,0.95)" }}>
      <Container maxWidth="xl" sx={{ py: "12px" }}>
        <Banner />
        <Typography
          variant="h2"
          textAlign="center"
          fontFamily="Audiowide"
          color="white"
          sx={{
            fontSize: { xs: "24px", md: "48px", xl: "60px" },
            mt: "16px",
            mb: "16px",
          }}
        >
          Crypto Moon Catz War!
        </Typography>    
        <Jackpot />   
        <Box sx={{display: 'flex', flexDirection: {xs: 'column', md: 'row'}, justifyContent: 'space-between', my: {xs: 1, sm: 2}}}>
          <Box sx={{ width: {xs: '100%', md: '16%'}}}>
            <Role />
          </Box>          
          <Box sx={{ width: {xs: '100%', md: '64%'} }}>
            <CurrentBattle />
            <FinishedBattle />
          </Box>
          <Box sx={{ width: {xs: '100%', md: '16%'}}}>
            <Winner />
          </Box>
        </Box>
        
      </Container>      
    </Box>
  );
};

export default Home;
